



















import { ApiService } from "@/x-vue/services/api.service";
import ChatBubbleSvg from "@/x-vue/svg/ChatBubbleSvg.vue";
import GoogleLogoSvg from "@/x-vue/svg/GoogleLogoSvg.vue";
import FacebookLogoSvg from "@/x-vue/svg/FacebookLogoSvg.vue";

import Vue from "vue";
import Component from "vue-class-component";

import { AppService } from "@/service/app.service";
import Register from "@/x-vue/components/user/Register.vue";

@Component({
  components: { ChatBubbleSvg, GoogleLogoSvg, FacebookLogoSvg, Register },
})
export default class extends Vue {
  app = AppService.instance;
  api = ApiService.instance;
  form = {};
  initKakao = false;

  async onLogin(): Promise<void> {
    console.log("Login::form", this.form);
    try {
      await this.app.login(this.form);
      await AppService.instance.loginOrRegisterIntoFirebase();
    } catch (e) {
      console.log(e);
      this.app.error(e);
    }
  }

  async onRegister(event: Event, form: Request): Promise<void> {
    try {
      await ApiService.instance.register(form);
      await AppService.instance.loginOrRegisterIntoFirebase();
      this.$router.push("/");
    } catch (e) {
      this.$app.error(e);
    }
  }
}
